import React, { useState } from "react";
import "./Passage.css";
import "./Passage_Core.css";
import PassageCore from "./Passage_Core";
import Draggable from "react-draggable";

function Passage({ setDarken, passages, setPassages, passage }) {
  const [hidden, setHidden] = useState(true);
  const [title, setTitle] = useState("Untitled Section");
  const [textile, setTextile] = useState("Double click here edit this.");

  const [bgcolor, setBgcolor] = useState("bluify");
  const [bgcolor2, setBgcolor2] = useState("bluify2");
  const stateList = {
    title,
    setTitle,
    textile,
    setTextile,
    passages,
    setPassages,
    hidden,
    setHidden,
    setDarken,
    passage,
  };

  function openPassage() {
    setHidden(false);
    setDarken(true);
    setPassages(
      passages.map((item) => {
        if (item.id === passage.id) {
          return {
            ...item,
            selected: false,
            paletteSelected: false,
          };
        }
        return item;
      })
    );
  }
  function deletePassage() {
    setPassages(passages.filter((e) => e.id !== passage.id));
  }

  function paletteOpen() {
    setPassages(
      passages.map((item) => {
        if (item.id === passage.id) {
          return {
            ...item,
            paletteSelected: !item.paletteSelected,
          };
        }
        return item;
      })
    );
  }
  const borderX = document.getElementsByClassName("line");
  function redSelect() {
    setBgcolor("redify");
    setBgcolor2("redify2");
    borderX[passage.index].style.border = "2px solid red";
  }
  function greenSelect() {
    setBgcolor("greenify");
    setBgcolor2("greenify2");
    borderX[passage.index].style.border = "2px solid greenyellow";
  }
  function blueSelect() {
    setBgcolor("bluify");
    setBgcolor2("bluify2");
    borderX[passage.index].style.border = "2px solid cyan";
  }
  function optionVisibility() {
    setPassages(
      passages.map((item) => {
        if (item.id === passage.id) {
          return {
            ...item,
            selected: true,
          };
        } else if (item.id !== passage.id) {
          return {
            ...item,
            selected: false,
            paletteSelected: false,
          };
        }
        return item;
      })
    );
  }

  return (
    <div>
      <PassageCore stateList={stateList} />
      <Draggable>
        <div className={`passage ${passage.selected ? "top" : ""}`}>
          <div
            className={`passage_index ${
              passage.empty ? "empty_mode" : ""
            } ${bgcolor}`}
            onMouseDown={optionVisibility}
            onDoubleClick={openPassage}
          >
            <h6 className="passage_text title">{title}</h6>
            <h6 className="passage_text textile">{textile}</h6>

            <div className={`line  ${passage.empty ? "visible" : ""}`}>
              <div className="line_inner">
                <div className={`${bgcolor2}`}></div>
                <div className={`${bgcolor2}`}></div>
                <div className={`${bgcolor2}`}></div>
              </div>
            </div>
          </div>

          {/* OUTSIDE EACH PASSAGE */}
          <div className="options_container">
            <div className={`options ${passage.selected ? "visible" : ""}`}>
              <button
                onClick={deletePassage}
                className="delete_btn"
                alt="delete"
              ></button>
              <button
                onClick={paletteOpen}
                className="palette_btn"
                alt="palette"
              ></button>
            </div>
            <div
              className={`palette ${passage.paletteSelected ? "visible" : ""}`}
            >
              <div
                className="red_pale palette_choice"
                onClick={redSelect}
              ></div>
              <div
                className="green_pale palette_choice"
                onClick={greenSelect}
              ></div>
              <div
                className="blue_pale palette_choice"
                onClick={blueSelect}
              ></div>
            </div>
          </div>
        </div>
      </Draggable>
    </div>
  );
}

export default Passage;
