import React from "react";
import "./Passage_Core.css";
function Passage_Core({ stateList }) {
  function fullscreen() {
    console.log("Fullscreening");
  }
  function propertyChange() {
    if (stateList.title === "" && stateList.textile === "") {
      stateList.setPassages(
        stateList.passages.map((item) => {
          if (item.id === stateList.passage.id) {
            return {
              ...item,
              empty: true,
            };
          }
          return item;
        })
      );
    } else {
      stateList.setPassages(
        stateList.passages.map((item) => {
          if (item.id === stateList.passage.id) {
            return {
              ...item,
              empty: false,
            };
          }
          return item;
        })
      );
    }
  }

  function exit() {
    stateList.setHidden(true);
    stateList.setDarken(false);
    propertyChange();
  }
  const titleHandler = (e) => {
    stateList.setTitle(e.target.value);
    if (stateList.title.length <= 16) {
    } else {
    }
  };
  const textareaHandler = (e) => {
    stateList.setTextile(e.target.value);
    if (stateList.textile.length <= 55) {
    } else {
    }
  };
  return (
    <div className={`Core_Container ${stateList.hidden ? "hide" : "unhide"}`}>
      <div className="Core_Header">
        <input
          onChange={titleHandler}
          type="text"
          className="core_title"
          autoComplete="off"
          placeholder={stateList.title}
        />
        <button className="coreButton Fullscreen" onClick={fullscreen}></button>
        <button className="coreButton Exit" onClick={exit}></button>
      </div>
      <textarea
        onChange={textareaHandler}
        name=""
        className="core_textile"
        cols="30"
        rows="10"
        placeholder={stateList.textile}
      ></textarea>
    </div>
  );
}

export default Passage_Core;
