import React, { useState } from "react";
import { RemoveScroll } from "react-remove-scroll";
import "./App.css";
import Form from "./components/Form";
import Passages from "./components/Passages";

function App() {
  const [passages, setPassages] = useState([]);
  const [darken, setDarken] = useState(false);
  const [count, setCount] = useState(0);

  return (
    <RemoveScroll>
      <div className="App">
        <div className={darken ? "darken" : ""}></div>
        <Form
          passages={passages}
          setPassages={setPassages}
          count={count}
          setCount={setCount}
        />
        <Passages
          passages={passages}
          setPassages={setPassages}
          setDarken={setDarken}
          count={count}
        />
      </div>
    </RemoveScroll>
  );
}

export default App;
