import React from "react";
import "./Form.css";
const Form = ({ passages, setPassages, count, setCount }) => {
  var index = count;
  const createPassage = (e) => {
    e.preventDefault();
    setPassages([
      ...passages,
      {
        id: Math.random() * 1000,
        empty: false,
        index: index,
        selected: false,
        paletteSelected: false,
      },
    ]);
    setCount((count) => count + 1);
  };
  return (
    <div className="App-header">
      <button onClick={createPassage} id="passage_button">
        <span>Create Section</span>
      </button>
    </div>
  );
};

export default Form;
