import React from "react";
import Passage from "./Passage";
function Passages({ passages, setPassages, setHidden, setDarken }) {
  return (
    <div className="passage__container">
      <div className="passage__list">
        {passages.map((passage) => (
          <Passage
            key={passage.id}
            passages={passages}
            setPassages={setPassages}
            setHidden={setHidden}
            setDarken={setDarken}
            passage={passage}
          />
        ))}
      </div>
    </div>
  );
}

export default Passages;
